
@import url("https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed");
body{font-family:'Roboto Condensed', sans-serif;color:#B6A05D;margin:0
}
button{font-family:'Roboto Condensed', sans-serif;border:1px solid #B6A05D;background:none;color:#B6A05D;text-transform:uppercase;padding:15px 20px;letter-spacing:1px
}
button--filled{color:white;background:#B6A05D
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-active{opacity:0
}
#header{position:fixed;left:15px;top:35px
}
#header button{font-size:16px;padding-bottom:2px
}
#header button.menu{border:none;padding:0;border-bottom:2px solid #B6A05D
}
#overlay{position:absolute;top:0;left:0;bottom:0;right:0;background:black;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
#overlay ul{padding:0;margin:0;text-transform:uppercase;text-align:center
}
#overlay ul li{list-style:none;font-size:18px;padding-bottom:5px
}
#overlay ul li a{color:#B6A05D;text-decoration:none
}
#overlay button{border:none
}
#overlay button.close{position:absolute;right:10px;top:30px;width:32px;height:32px
}
#overlay button.close:before,#overlay button.close:after{position:absolute;left:15px;top:0;content:' ';height:33px;width:2px;background-color:#B6A05D
}
#overlay button.close:before{transform:rotate(45deg)
}
#overlay button.close:after{transform:rotate(-45deg)
}



